import React, { useState, useEffect } from 'react';

import phone from './phone.svg'

import HomePage from './containers/HomePage'
import DotsBg from './components/DotsBg'
import browser from './components/utils/browser'
import Box from './components/Box'
import Image from './components/Image'
import Text from './components/Text'
import FullpageVerticalCenter from './components/FullpageVerticalCenter'

const Alert = () => (
  <DotsBg color="yellow" textAlign="center" fontSize="5em" height="100vh">
    <FullpageVerticalCenter>
      <Box px="25%">
        <Image src={phone} />
      </Box>
      <Box pt="1em">
        <Text>請使用橫式觀看</Text>
        <Text>以獲得最佳體驗</Text>
      </Box>
    </FullpageVerticalCenter>
  </DotsBg>
)

const isMobile = browser.getPlatformType() !== 'desktop'

function App() {
  const [nowOrientation, setOrientation] = useState(window.orientation)
  useEffect(() => {
    window.addEventListener("orientationchange", function() {
      setOrientation(window.orientation)
    }, false);
  }, [])
  if (isMobile) {
    switch (nowOrientation) {
      case 0:
        return <Alert />
      case 180:
        return <Alert />
      default:
        return <HomePage />;
    }
  }
  return <HomePage />;
}

export default App;
